<template>
    <div id="app">
        <!--  -->
        <van-nav-bar 
            title="" 
            :left-arrow="false"
            @click-right="handleRightClick"
            :right-text="currentUser ? '' : '/'"
        />
        
        <!--  -->
        <van-notice-bar v-if="currentUser" :text="`: ${currentUser.username}`" />
        
        <!--  -->
        <div class="container">
            <!-- / -->
            <div v-if="!currentUser && showAuthForm" class="auth-form">
                <van-tabs v-model="activeAuthTab" title-active-color="#1989fa">
                    <van-tab title="">
                        <van-form @submit="handleLogin">
                            <van-field
                                v-model="loginForm.username"
                                name="username"
                                label=""
                                placeholder=""
                                :rules="[{ required: true, message: '' }]"
                            />
                            <van-field
                                v-model="loginForm.password"
                                type="password"
                                name="password"
                                label=""
                                placeholder=""
                                :rules="[{ required: true, message: '' }]"
                            />
                            <div style="margin: 16px;">
                                <van-button round block type="primary" native-type="submit">
                                    
                                </van-button>
                            </div>
                        </van-form>
                    </van-tab>
                    <van-tab title="">
                        <van-form @submit="handleRegister">
                            <van-field
                                v-model="registerForm.username"
                                name="username"
                                label=""
                                placeholder=""
                                :rules="[{ required: true, message: '' }]"
                            />
                            <van-field
                                v-model="registerForm.password"
                                type="password"
                                name="password"
                                label=""
                                placeholder=""
                                :rules="[{ required: true, message: '' }]"
                            />
                            <van-field
                                v-model="registerForm.email"
                                name="email"
                                label=""
                                placeholder=""
                                :rules="[
                                    { required: true, message: '' },
                                    { type: 'email', message: '' }
                                ]"
                            />
                            <div style="margin: 16px;">
                                <van-button round block type="primary" native-type="submit">
                                    
                                </van-button>
                            </div>
                        </van-form>
                    </van-tab>
                </van-tabs>
            </div>
            
            <!--  -->
            <div v-if="currentUser" class="function-tabs">
                <van-tabs v-model="activeTab" title-active-color="#1989fa">
                    <!--  -->
                    <van-tab title="">
                        <!--  -->
                        <div class="blog-operations" style="padding: 15px; background-color: white; margin-bottom: 10px;">
                            <van-button @click="fetchAllBlogs" type="primary" size="small" style="margin-right: 10px;">
                                
                            </van-button>
                            <van-button @click="showAddBlogDialog = true" type="success" size="small" style="margin-right: 10px;">
                                
                            </van-button>
                            <van-search
                                v-model="searchKeyword"
                                placeholder="..."
                                @search="handleSearch"
                                style="width: 300px;"
                            />
                        </div>
                        
                        <!--  -->
                        <van-dialog v-model:show="showAddBlogDialog" title="">
                            <van-form @submit="handleAddBlog">
                                <van-field
                                    v-model="newBlog.title"
                                    name="title"
                                    label=""
                                    placeholder=""
                                    :rules="[{ required: true, message: '' }]"
                                />
                                <van-field
                                    v-model="newBlog.content"
                                    name="content"
                                    label=""
                                    type="textarea"
                                    rows="5"
                                    placeholder=""
                                    :rules="[{ required: true, message: '' }]"
                                />
                                <div style="margin-top: 16px;">
                                    <van-button round block type="primary" native-type="submit">
                                        
                                    </van-button>
                                </div>
                            </van-form>
                        </van-dialog>
                        
                        <!--  -->
                        <div v-if="allBlogs.length > 0" class="blog-list-container">
                            <h3 style="margin-top: 0; padding: 15px; background-color: white;"></h3>
                            <van-list>
                                <!--  -->
                                <div 
                                    v-for="blog in allBlogs" 
                                    :key="blog.id" 
                                    class="blog-card"
                                    @click="showBlogDetail(blog)"
                                >
                                    <div class="blog-card-header">
                                        <h4 class="blog-card-title">{{ blog.title }}</h4>
                                        <div class="blog-card-meta">
                                            <span>: {{ blog.username }}</span>
                                            <span>: {{ formatDate(blog.date) }}</span>
                                        </div>
                                    </div>
                                    <div class="blog-card-content">
                                        {{ blog.content.substring(0, 150) }}{{ blog.content.length > 150 ? '...' : '' }}
                                    </div>
                                    <div class="blog-card-footer">
                                        <span class="blog-card-stats">
                                            <van-icon name="like-o" size="14" /> {{ blog.likes || 0 }}
                                        </span>
                                        <span class="blog-card-stats">
                                            <van-icon name="comment-o" size="14" /> {{ blog.commentCount || 0 }}
                                        </span>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                        
                        <!--  -->
                        <div v-if="allBlogs.length === 0 && blogsLoaded" style="background-color: white; padding: 15px; text-align: center; color: #999;">
                            
                        </div>
                        
                        <!--  -->
                        <van-dialog v-model:show="showBlogDetailDialog" :title="currentBlog.title" class="blog-detail-dialog">
                            <div class="blog-detail">
                                <div class="blog-meta">
                                    <span>: {{ currentBlog.username }}</span>
                                    <span>: {{ formatDate(currentBlog.date) }}</span>
                                </div>
                                <div class="blog-content">
                                    {{ currentBlog.content }}
                                </div>
                                
                                <!--  -->
                                <div class="blog-actions">
                                    <van-button 
                                        icon="like-o" 
                                        size="small"
                                        :color="isLiked ? '#ee0a24' : ''"
                                        @click="handleLike"
                                    >
                                        {{ totalLikes }} 
                                    </van-button>
                                </div>
                                
                                <!--  -->
                                <div class="blog-comments">
                                    <h4> ({{ comments.length }})</h4>
                                    <van-list>
                                        <van-cell 
                                            v-for="comment in comments" 
                                            :key="comment.date" 
                                            :title="comment.username"
                                            :value="formatDate(comment.date)"
                                        >
                                            <template #label>{{ comment.content }}</template>
                                        </van-cell>
                                    </van-list>
                                    
                                    <!--  -->
                                    <van-field
                                        v-model="newComment"
                                        placeholder="..."
                                        :rows="2"
                                        type="textarea"
                                    />
                                    <van-button 
                                        size="small" 
                                        type="primary"
                                        @click="handleAddComment"
                                        style="margin-top: 10px;"
                                    >
                                        
                                    </van-button>
                                </div>
                            </div>
                        </van-dialog>
                    </van-tab>
                    
                    <!--  -->
                    <van-tab title="">
                        <div class="personal-blogs">
                            <van-button @click="fetchMyBlogs" type="primary" size="small" style="margin: 15px;">
                                
                            </van-button>
                            
                            <div v-if="myBlogs.length > 0" style="background-color: white; padding: 15px; margin-bottom: 10px;">
                                <van-list>
                                    <van-cell 
                                        v-for="blog in myBlogs" 
                                        :key="blog.date" 
                                        :title="blog.title"
                                        :value="`: ${formatDate(blog.date)}`"
                                        is-link
                                        @click="showBlogDetail(blog)"
                                    >
                                        <template #label>
                                            {{ blog.content.substring(0, 50) }}{{ blog.content.length > 50 ? '...' : '' }}
                                        </template>
                                        <template #extra>
                                            <van-button 
                                                size="mini" 
                                                type="danger" 
                                                @click.stop="handleDeleteBlog(blog.date, $event)"
                                            >
                                                
                                            </van-button>
                                        </template>
                                    </van-cell>
                                </van-list>
                            </div>
                            
                            <div v-if="myBlogs.length === 0 && myBlogsLoaded" style="background-color: white; padding: 15px; text-align: center; color: #999;">
                                
                            </div>
                        </div>
                    </van-tab>
                    
                    <!--  -->
                    <van-tab title="">
                        <div class="friends-section">
                            <van-search
                                v-model="friendSearch"
                                placeholder="..."
                                @search="searchUsers"
                                style="margin: 15px;"
                            />
                            
                            <!--  -->
                            <div v-if="searchResults.length > 0" style="background-color: white; padding: 15px; margin: 0 15px 15px;">
                                <h4></h4>
                                <van-list>
                                    <van-cell 
                                        v-for="user in searchResults" 
                                        :key="user.username" 
                                        :title="user.username"
                                    >
                                        <template #extra>
                                            <van-button 
                                                size="mini" 
                                                type="primary"
                                                @click="handleAddFriend(user.username)"
                                                :disabled="user.username === currentUser.username || friends.includes(user.username)"
                                            >
                                                {{ friends.includes(user.username) ? '' : '' }}
                                            </van-button>
                                        </template>
                                    </van-cell>
                                </van-list>
                            </div>
                            
                            <!--  -->
                            <div style="background-color: white; padding: 15px; margin: 0 15px 15px;">
                                <h4></h4>
                                <van-button @click="fetchFriends" type="primary" size="small" style="margin-bottom: 10px;">
                                    
                                </van-button>
                                
                                <van-list v-if="friends.length > 0">
                                    <van-cell 
                                        v-for="friend in friends" 
                                        :key="friend" 
                                        :title="friend"
                                        is-link
                                        @click="viewFriendBlogs(friend)"
                                    />
                                </van-list>
                                
                                <div v-if="friends.length === 0 && friendsLoaded" style="text-align: center; color: #999; padding: 10px;">
                                    
                                </div>
                            </div>
                            
                            <!--  -->
                            <div style="background-color: white; padding: 15px; margin: 0 15px 15px;">
                                <h4></h4>
                                <van-button @click="fetchFriendApplys" type="primary" size="small" style="margin-bottom: 10px;">
                                    
                                </van-button>
                                
                                <van-list v-if="friendApplys.length > 0">
                                    <van-cell 
                                        v-for="apply in friendApplys" 
                                        :key="apply.username" 
                                        :title="apply.username"
                                    >
                                        <template #extra>
                                            <div>
                                                <van-button 
                                                    size="mini" 
                                                    type="success"
                                                    @click="handleApproveFriend(apply.username)"
                                                    :disabled="apply.isReplied"
                                                >
                                                    
                                                </van-button>
                                                <van-button 
                                                    size="mini" 
                                                    type="danger"
                                                    @click="handleRejectFriend(apply.username)"
                                                    :disabled="apply.isReplied"
                                                >
                                                    
                                                </van-button>
                                            </div>
                                        </template>
                                    </van-cell>
                                </van-list>
                                
                                <div v-if="friendApplys.length === 0 && applysLoaded" style="text-align: center; color: #999; padding: 10px;">
                                    
                                </div>
                            </div>
                        </div>
                    </van-tab>
                    
                    <!--  -->
                    <van-tab title="">
                        <div class="recognition-section">
                            <!--  -->
                            <div class="recognition-card">
                                <h4></h4>
                                <van-uploader
                                    :after-read="handleSpeechUpload"
                                    accept="audio/*"
                                    :max-size="10 * 1024 * 1024"
                                >
                                    <van-button type="primary" size="small"></van-button>
                                </van-uploader>
                                <div v-if="speechResult" class="result-box">
                                    <p>: {{ speechResult }}</p>
                                </div>
                            </div>
                            
                            <!--  -->
                            <div class="recognition-card">
                                <h4></h4>
                                <van-uploader
                                    :after-read="handleToneUpload"
                                    accept="audio/*"
                                    :max-size="10 * 1024 * 1024"
                                >
                                    <van-button type="primary" size="small"></van-button>
                                </van-uploader>
                                <div v-if="toneResult" class="result-box">
                                    <p>: {{ toneResult.f0_mean }}</p>
                                    <p>: {{ toneResult.f0_max }}</p>
                                    <p>: {{ toneResult.f0_min }}</p>
                                    <p>: {{ toneResult.tone_type }}</p>
                                </div>
                            </div>
                            
                            <!--  -->
                            <div class="recognition-card">
                                <h4></h4>
                                <van-uploader
                                    :after-read="handleEmotionUpload"
                                    accept="image/*"
                                    :max-size="5 * 1024 * 1024"
                                >
                                    <van-button type="primary" size="small"></van-button>
                                </van-uploader>
                                <div v-if="emotionResult && emotionResult.predictions.length > 0" class="result-box">
                                    <div v-for="pred in emotionResult.predictions" :key="pred.bounding_box.join(',')">
                                        <p>: {{ pred.emotion }} (: {{ pred.confidence.toFixed(2) }})</p>
                                    </div>
                                </div>
                            </div>
                            
                            <!--  -->
                            <div class="recognition-card">
                                <h4></h4>
                                <van-button 
                                    type="primary" 
                                    size="small"
                                    @click="togglePoseDetection"
                                >
                                    {{ isPoseDetecting ? '' : '' }}
                                </van-button>
                                
                                <div v-if="isPoseDetecting" class="pose-container">
                                    <img :src="poseVideoUrl" alt="" class="pose-video">
                                    <div class="pose-info">
                                        <p>: {{ poseData.pose }}</p>
                                        <p>FPS: {{ poseData.fps }}</p>
                                        <p>: {{ poseData.score }}</p>
                                    </div>
                                </div>
                            </div>
                            
                            <!--  -->
                            <div class="recognition-card">
                                <h4>AI</h4>
                                <van-field
                                    v-model="chatMessage"
                                    placeholder="..."
                                    :rows="3"
                                    type="textarea"
                                />
                                <van-button 
                                    type="primary" 
                                    size="small"
                                    @click="sendChatMessage"
                                    style="margin-top: 10px;"
                                    :loading="isChatLoading"
                                >
                                    
                                </van-button>
                                
                                <div v-if="chatResponse" class="result-box">
                                    <p>AI: {{ chatResponse }}</p>
                                </div>
                            </div>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
        </div>
        
        <!--  -->
        <van-button 
            type="info" 
            size="mini"
            @click="checkHealthStatus"
            style="position: fixed; bottom: 20px; right: 20px; z-index: 100;"
        >
            
        </van-button>
    </div>
</template>

<script>
import { 
    NavBar, Tabs, Tab, Form, Field, Button, List, 
    Dialog, Search, Uploader, NoticeBar, Toast, Icon 
} from 'vant';
import axios from 'axios';

// API
const API_BASE_URL = 'https://123.56.203.202';

// axios
const api = axios.create({
    baseURL: API_BASE_URL,
    timeout: 30000,
    withCredentials: true
});

export default {
    components: {
        VanNavBar: NavBar,
        VanTabs: Tabs,
        VanTab: Tab,
        VanForm: Form,
        VanField: Field,
        VanButton: Button,
        VanList: List,
        VanDialog: Dialog,
        VanSearch: Search,
        VanUploader: Uploader,
        VanNoticeBar: NoticeBar,
        VanIcon: Icon,
        
    },
    data() {
        return {
            // 
            currentUser: null,
            showAuthForm: true,
            activeAuthTab: 0,
            loginForm: {
                username: '',
                password: ''
            },
            registerForm: {
                username: '',
                password: '',
                email: ''
            },
            
            // 
            activeTab: 0,
            
            // 
            allBlogs: [],
            myBlogs: [],
            blogsLoaded: false,
            myBlogsLoaded: false,
            showAddBlogDialog: false,
            newBlog: {
                title: '',
                content: ''
            },
            showBlogDetailDialog: false,
            currentBlog: {},
            comments: [],
            newComment: '',
            totalLikes: 0,
            isLiked: false,
            searchKeyword: '',
            
            // 
            friends: [],
            friendsLoaded: false,
            friendApplys: [],
            applysLoaded: false,
            friendSearch: '',
            searchResults: [],
            
            // 
            speechResult: '',
            toneResult: null,
            emotionResult: null,
            isPoseDetecting: false,
            poseVideoUrl: '',
            poseData: {
                pose: "",
                fps: 0,
                score: 0
            },
            poseInterval: null,
            
            // 
            chatMessage: '',
            chatResponse: '',
            isChatLoading: false
        };
    },
    mounted() {
        // 
        const savedUser = localStorage.getItem('currentUser');
        if (savedUser) {
            try {
                this.currentUser = JSON.parse(savedUser);
                this.showAuthForm = false;
                // 
                this.fetchAllBlogs();
            } catch (e) {
                console.error('', e);
                localStorage.removeItem('currentUser');
            }
        }
    },
    methods: {
        // 
        formatDate(dateStr) {
            if (!dateStr) return '';
            const date = new Date(dateStr);
            return date.toLocaleString();
        },
        
        // 
        handleRightClick() {
            if (this.currentUser) {
                // 
                localStorage.removeItem('currentUser');
                this.currentUser = null;
                this.showAuthForm = true;
                Toast('');
            } else {
                // /
                this.showAuthForm = true;
            }
        },
        
        // 
        async handleLogin(values) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/sqlLogin', {
                    username: values.username,
                    password: values.password
                });
                
                Toast.clear();
                
                if (data.success) {
                    this.currentUser = {
                        username: data.username,
                        id: data.id
                    };
                    localStorage.setItem('currentUser', JSON.stringify(this.currentUser));
                    this.showAuthForm = false;
                    // 
                    this.fetchAllBlogs();
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleRegister(values) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/register', {
                    username: values.username,
                    password: values.password,
                    email: values.email
                });
                
                Toast.clear();
                
                if (data.success) {
                    Toast('');
                    this.activeAuthTab = 0; // 
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async fetchAllBlogs() {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get('/getBlogs');
                
                Toast.clear();
                
                this.allBlogs = data || [];
                this.blogsLoaded = true;
                Toast(` ${this.allBlogs.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async fetchMyBlogs() {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get(`/personBlogs/${this.currentUser.username}`);
                
                Toast.clear();
                this.myBlogs = data.a || [];
                this.myBlogsLoaded = true;
                Toast(` ${this.myBlogs.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleAddBlog(values) {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post(`/submitBlogs/${this.currentUser.username}`, {
                    title: values.title,
                    content: values.content
                });
                
                Toast.clear();
                
                if (data.message === '') {
                    this.showAddBlogDialog = false;
                    this.newBlog = { title: '', content: '' };
                    this.fetchMyBlogs(); // 
                    this.fetchAllBlogs(); // 
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async showBlogDetail(blog) {
            this.currentBlog = blog;
            this.showBlogDetailDialog = true;
            
            // 
            try {
                const { data } = await api.get('/getComments');
                this.comments = data.filter(comment => comment.blogDate === blog.date) || [];
            } catch (error) {
                console.error('', error);
                this.comments = [];
            }
            
            // 
            try {
                const { data } = await api.get(`/getTotalLikes/${blog.username}/${blog.date}`);
                this.totalLikes = data.number || 0;
                
                // 
                this.isLiked = false;
            } catch (error) {
                console.error('', error);
                this.totalLikes = 0;
            }
        },
        
        // 
        async handleDeleteBlog(date, event) {
            // 
            if (event) event.stopPropagation();
            
            try {
                const confirm = await Dialog.confirm({
                    title: '',
                    message: '',
                });
                
                if (confirm) {
                    Toast.loading({
                        message: '...',
                        forbidClick: true,
                    });
                    
                    const { data } = await api.delete('/deleteBlogsByTime', {
                        data: { date }
                    });
                    
                    Toast.clear();
                    
                    if (data.message === '') {
                        Toast('');
                        this.fetchAllBlogs();
                        this.fetchMyBlogs();
                        this.showBlogDetailDialog = false;
                    } else {
                        Toast(data.message || '');
                    }
                }
            } catch (error) {
                if (error.name !== 'DialogCancel') { // 
                    Toast.clear();
                    console.error('', error);
                    Toast('');
                }
            }
        },
        
        // 
        async handleAddComment() {
            if (!this.currentUser || !this.newComment.trim() || !this.currentBlog.date) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post(
                    `/addComment/${this.currentBlog.username}/${this.currentBlog.date}`,
                    {
                        content: this.newComment.trim(),
                        username: this.currentUser.username
                    }
                );
                
                Toast.clear();
                
                if (data.message === '') {
                    this.newComment = '';
                    // 
                    const { data: commentsData } = await api.get('/getComments');
                    this.comments = commentsData.filter(comment => comment.blogDate === this.currentBlog.date) || [];
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleLike() {
            if (!this.currentUser || !this.currentBlog.date) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post(
                    `/addLike/${this.currentBlog.username}/${this.currentBlog.date}`,
                    {
                        likeUsername: this.currentUser.username
                    }
                );
                
                Toast.clear();
                
                if (data.message === '') {
                    this.totalLikes = data.totalLikes || 0;
                    this.isLiked = true;
                    Toast('');
                } else if (data.message === '') {
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleSearch() {
            if (!this.searchKeyword.trim()) {
                this.fetchAllBlogs();
                return;
            }
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get(`/search?keyword=${encodeURIComponent(this.searchKeyword.trim())}`);
                
                Toast.clear();
                this.allBlogs = data || [];
                this.blogsLoaded = true;
                Toast(` ${this.allBlogs.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async fetchFriends() {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/getFriends', {
                    username: this.currentUser.username
                });
                
                Toast.clear();
                this.friends = data.friends || [];
                this.friendsLoaded = true;
                Toast(` ${this.friends.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async fetchFriendApplys() {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get(`/getApplys/${this.currentUser.username}`);
                
                Toast.clear();
                this.friendApplys = data.friendApplys || [];
                this.applysLoaded = true;
                Toast(` ${this.friendApplys.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async searchUsers() {
            if (!this.friendSearch.trim()) {
                this.searchResults = [];
                return;
            }
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get(`/search?keyword=${encodeURIComponent(this.friendSearch.trim())}`);
                
                Toast.clear();
                
                // 
                const users = new Set();
                data.forEach(blog => users.add(blog.username));
                
                this.searchResults = Array.from(users).map(username => ({ username }));
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleAddFriend(username) {
            if (!this.currentUser || username === this.currentUser.username) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/addFriend', {
                    username: this.currentUser.username,
                    goalUsername: username
                });
                
                Toast.clear();
                
                if (data.message === '') {
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleApproveFriend(username) {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/updateFriendApply', {
                    currentUsername: this.currentUser.username,
                    friendUsername: username,
                    status: true
                });
                
                Toast.clear();
                
                if (data.message.includes('')) {
                    Toast('');
                    this.fetchFriendApplys();
                    this.fetchFriends();
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleRejectFriend(username) {
            if (!this.currentUser) return;
            
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/updateFriendApply', {
                    currentUsername: this.currentUser.username,
                    friendUsername: username,
                    status: false
                });
                
                Toast.clear();
                
                if (data.message.includes('')) {
                    Toast('');
                    this.fetchFriendApplys();
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async viewFriendBlogs(username) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get(`/personBlogs/${username}`);
                
                Toast.clear();
                this.allBlogs = data.a || [];
                this.blogsLoaded = true;
                this.activeTab = 0; // 
                Toast(` ${username}  ${this.allBlogs.length} `);
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleSpeechUpload(file) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const formData = new FormData();
                formData.append('audio', file.file);
                
                const { data } = await api.post('/recognize_speech', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });
                
                Toast.clear();
                
                if (data.status === 'success') {
                    this.speechResult = data.text;
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleToneUpload(file) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const formData = new FormData();
                formData.append('audio', file.file);
                
                const { data } = await api.post('/extract_tone', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });
                
                Toast.clear();
                
                if (data.status === 'success') {
                    this.toneResult = data.data;
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async handleEmotionUpload(file) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const formData = new FormData();
                formData.append('image', file.file);
                
                const { data } = await api.post('/predict/image', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });
                
                Toast.clear();
                
                if (data.status === 'success') {
                    this.emotionResult = data;
                    Toast('');
                } else {
                    Toast(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        togglePoseDetection() {
            if (this.isPoseDetecting) {
                // 
                this.isPoseDetecting = false;
                this.poseVideoUrl = '';
                if (this.poseInterval) {
                    clearInterval(this.poseInterval);
                    this.poseInterval = null;
                }
            } else {
                // 
                this.isPoseDetecting = true;
                // URL
                this.poseVideoUrl = `${API_BASE_URL}/video_feed`;
                
                // 
                this.poseInterval = setInterval(async () => {
                    try {
                        const { data } = await api.get('/detection_data');
                        this.poseData = data;
                    } catch (error) {
                        console.error('', error);
                    }
                }, 1000);
            }
        },
        
        // 
        async sendChatMessage() {
            if (!this.chatMessage.trim()) return;
            
            try {
                this.isChatLoading = true;
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/api/chat', {
                    prompt: this.chatMessage.trim()
                });
                
                Toast.clear();
                this.isChatLoading = false;
                
                if (data.status === 'success') {
                    this.chatResponse = data.result;
                } else {
                    Toast(data.result || '');
                }
            } catch (error) {
                Toast.clear();
                this.isChatLoading = false;
                console.error('', error);
                Toast('');
            }
        },
        
        // 
        async checkHealthStatus() {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.get('/health');
                
                Toast.clear();
                
                const statusText = data.status === 'healthy' ? '' : '';
                const details = [
                    `: ${statusText}`,
                    `: ${data.database_connection ? '' : ''}`,
                    `: ${data.tone_extraction ? '' : ''}`,
                    `: ${data.pose_detection ? '' : ''}`,
                    `: ${data.speech_recognition ? '' : ''}`,
                    `: ${data.emotion_recognition ? '' : ''}`,
                    `: ${data.chat_function ? '' : ''}`
                ];
                
                Dialog.alert({
                    title: '',
                    message: details.join('\n'),
                    confirmButtonText: ''
                });
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast('');
            }
        }
    },
    beforeUnmount() {
        // 
        if (this.poseInterval) {
            clearInterval(this.poseInterval);
        }
        // 
        Toast.clear();
    }
};
</script>

<style>
