    font-size: 14px;
}

.blog-card-footer {
    display: flex;
    justify-content: flex-end;
    color: #888;
    font-size: 12px;
}

.blog-card-stats {
    margin-left: 15px;
    display: flex;
    align-items: center;
}

.blog-card-stats van-icon {
    margin-right: 4px;
}

/*  */
.blog-detail-dialog {
    max-width: 90%;
    width: 800px;
}

.blog-detail {
    max-height: 70vh;
    overflow-y: auto;
    padding-right: 10px;
}

.blog-meta {
    margin-bottom: 15px;
    color: #666;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
}

.blog-content {
    margin-bottom: 20px;
    line-height: 1.8;
