}

.pose-container {
    margin-top: 15px;
}

.pose-video {
    width: 100%;
    max-width: 640px;
    border-radius: 4px;
}

.pose-info {
    margin-top: 10px;
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 4px;
}
</style>
