          <div class="message-bubble typing">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
    
    <!--  -->
    <div id="dialog_bottombar">
      <div id="dialog_bottombar_inside">
        <van-field 
