            @click="captureImage"
            :disabled="!isCameraActive || isProcessingImage"
          >
            <van-icon name="camera" size="18" style="margin-right: 5px;" />
            {{ isProcessingImage ? '...' : '' }}
          </van-button>
          
          <van-button 
            type="info" 
            round 
            @click="startContinuousAnalysis"
            :disabled="!isCameraActive || isAnalyzingContinuously"
          >
            <van-icon name="play" size="18" style="margin-right: 5px;" />
            {{ isAnalyzingContinuously ? '' : '' }}
          </van-button>
        </div>
      </div>
    </van-popup>
    
    <!--  -->
    <van-popup
      v-model:show="isReportVisible"
      position="center"
      :style="{ width: '90%', maxWidth: '800px', height: '90%', overflow: 'auto' }"
    >
      <div class="report-container">
        <div class="report-header">
          <h2></h2>
          <van-icon name="close" @click="isReportVisible = false" class="close-icon" />
        </div>
        
        <div v-if="reportLoading" class="report-loading">
          <van-loading type="spinner" color="#1677ff" size="30" />
          <p>...</p>
        </div>
        
        <div v-else-if="reportError" class="report-error">
          <van-icon name="warning-circle" color="#f53f3f" size="24" />
          <p>{{ reportError }}</p>
          <van-button @click="generateReportFromStorage" type="primary" round></van-button>
        </div>
        
        <div v-else class="report-content">
          <!--  -->
          <div class="radar-chart-container">
            <h3 class="chart-title"></h3>
            <div class="chart-wrapper">
              <canvas ref="radarChart" width="400" height="300"></canvas>
            </div>
          </div>
          
          <!--  -->
          <div class="ability-details-container">
            <h3 class="details-title"></h3>
            <div class="details-list">
              <div v-for="(item, index) in reportIndicators" :key="index" class="detail-item">
                <span class="indicator-name">{{ item }}</span>
                <span class="score-value" :class="getScoreClass(reportValues[index])">
                  {{ reportValues[index] }} ({{ getScoreLevel(reportValues[index]) }})
                </span>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="analysis-container">
            <h3 class="analysis-title"></h3>
            
            <div class="analysis-section">
              <h4></h4>
              <p>{{ professionalKnowledgeAnalysis || '' }}</p>
              <p v-if="firstQuestion"><strong>:</strong> {{ firstQuestion }}</p>
              <p v-if="firstResult"><strong>:</strong> {{ firstResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4></h4>
              <p>{{ skillMatchAnalysis || '' }}</p>
              <p v-if="secondQuestion"><strong>:</strong> {{ secondQuestion }}</p>
              <p v-if="secondResult"><strong>:</strong> {{ secondResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4></h4>
              <p>{{ languageExpressionAnalysis || '' }}</p>
              <p v-if="thirdQuestion"><strong>:</strong> {{ thirdQuestion }}</p>
              <p v-if="thirdResult"><strong>:</strong> {{ thirdResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4></h4>
              <p>{{ logicalThinkingAnalysis || '' }}</p>
              <p v-if="fourthQuestion"><strong>:</strong> {{ fourthQuestion }}</p>
              <p v-if="fourthResult"><strong>:</strong> {{ fourthResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4></h4>
              <p>{{ innovationAbilityAnalysis || '' }}</p>
              <p v-if="fifthQuestion"><strong>:</strong> {{ fifthQuestion }}</p>
              <p v-if="fifthResult"><strong>:</strong> {{ fifthResult }}</p>
            </div>
          </div>
          
          <!--  -->
          <div class="summary-container">
            <h3 class="summary-title"></h3>
            <div class="summary-content">
              <p>{{ comprehensiveEvaluation || '' }}</p>
              
              <h4></h4>
              <ul v-if="improvementSuggestions.length">
                <li v-for="(suggestion, index) in improvementSuggestions" :key="index">
                  {{ suggestion }}
                </li>
              </ul>
              <p v-else></p>
            </div>
          </div>
          
          <!--  -->
          <div class="response-body-container">
            <h3 class="response-body-title"></h3>
            <div class="response-body-content">
              <pre>{{ formattedReportResponse }}</pre>
            </div>
          </div>
        </div>
      </div>
    </van-popup>
    
    <!--  -->
    <div v-if="isRecording" class="recording-indicator">
      <div class="recording-dot"></div>
      <p>...{{ recordingSeconds }}s</p>
      <p></p>
    </div>
    
    <!--  -->
    <div 
      class="status-bar" 
      :class="statusClass"
      style="z-index: 998;"
    >
      {{ statusText }}
    </div>
  </div>
</template>

<script>
import { Chart, registerables, RadarController } from 'chart.js';

export default {
  data() {
    return {
      name: this.$route.params.name,
      message: "",
      messages: [],
      isTyping: false,
      typingId: "",
      API_BASE_URL: 'https://123.56.203.202',
      statusText: '...',
      statusClass: 'text-xs text-gray-500 mt-2 text-center',
      commonHeaders: {
        'ngrok-skip-browser-warning': '1'
      },
      
      // 
      isRecording: false,
      mediaRecorder: null,
      audioBlob: null,
      recordingSeconds: 0,
      recordingTimer: null,
      audioContext: null,
      audioElements: {},
      mediaStream: null,
      audioChunks: [],
      transcribeCount: 0, // 
      
      // 
      isCameraModalVisible: false,
      isCameraActive: false,
      isLoadingCamera: false,
      cameraError: null,
      isProcessingImage: false,
      isAnalyzingContinuously: false,
      continuousAnalysisTimer: null,
      detectionInterval: null,
      videoStream: null,
      
      // 
      isReportVisible: false,
      reportLoading: false,
      reportError: '',
      chart: null,
      reportResponse: null, // 
      
      //  - 
      reportIndicators: [
        '',
        '',
        '',
        '',
        ''
      ],
      reportValues: [60, 60, 60, 60, 60],
      
      // 
      firstQuestion: '',
      secondQuestion: '',
      thirdQuestion: '',
      fourthQuestion: '',
      fifthQuestion: '',
      firstResult: '',
      secondResult: '',
      thirdResult: '',
      fourthResult: '',
      fifthResult: '',
      
      // 
      professionalKnowledgeAnalysis: '', // 
      skillMatchAnalysis: '', // 
      languageExpressionAnalysis: '', // 
      logicalThinkingAnalysis: '', // 
      innovationAbilityAnalysis: '', // 
      comprehensiveEvaluation: '',
      improvementSuggestions: []
    };
  },
  computed: {
    // 
    formattedReportResponse() {
      if (!this.reportResponse) return '';
      return JSON.stringify(this.reportResponse, null, 2);
    }
  },
  mounted() {
    this.testConnection();
    this.initContainerHeight();
    window.addEventListener('resize', this.initContainerHeight);
    this.checkRecordingSupport();
    
    // localStorage
    this.loadStoredQuestionsAndAnswers();
    
    // Chart.js
    Chart.register(...registerables);
    Chart.register(RadarController);
    window.Chart = Chart;
    
    // 
    this.startInterviewProcess();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.initContainerHeight);
    this.stopRecording();
    Object.values(this.audioElements).forEach(audio => {
      if (audio) {
        audio.pause();
      }
    });
    
    // 
    this.stopCamera();
    if (this.continuousAnalysisTimer) {
      clearInterval(this.continuousAnalysisTimer);
    }
    if (this.detectionInterval) {
      clearInterval(this.detectionInterval);
    }
    
    // 
    if (this.chart) {
      this.chart.destroy();
    }
  },
  methods: {
    // localStorage
    loadStoredQuestionsAndAnswers() {
      this.firstQuestion = localStorage.getItem("firstQuestion") || '';
      this.secondQuestion = localStorage.getItem("secondQuestion") || '';
      this.thirdQuestion = localStorage.getItem("thirdQuestion") || '';
      this.fourthQuestion = localStorage.getItem("fourthQuestion") || '';
      this.fifthQuestion = localStorage.getItem("fifthQuestion") || '';
      
      this.firstResult = localStorage.getItem("firstResult") || '';
      this.secondResult = localStorage.getItem("secondResult") || '';
      this.thirdResult = localStorage.getItem("thirdResult") || '';
      this.fourthResult = localStorage.getItem("fourthResult") || '';
      this.fifthResult = localStorage.getItem("fifthResult") || '';
    },
    
    // 
    startInterviewProcess() {
      // avatarPlatform2
      const avatarPlatform2 = true;
      
      if (avatarPlatform2) {
        //  - 
        let text = `${localStorage.getItem("username") || ""}${localStorage.getItem("career") || ""}60`;

        this.addMessage('ai', text, 'text');
        
        // 30
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `${career || ''}`;
          // localStorage
          if (!localStorage.getItem("firstQuestion")) {
            localStorage.setItem("firstQuestion", text);
          }
          this.firstQuestion = text;
          this.addMessage('ai', text, 'text');
        }, 30000);
        
        // 90
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `${career || ''}`;
          if (!localStorage.getItem("secondQuestion")) {
            localStorage.setItem("secondQuestion", text);
          }
          this.secondQuestion = text;
          this.addMessage('ai', text, 'text');
        }, 90000);
        
        // 150
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `${career || ''}`;
          if (!localStorage.getItem("thirdQuestion")) {
            localStorage.setItem("thirdQuestion", text);
          }
          this.thirdQuestion = text;
          this.addMessage('ai', text, 'text');
        }, 150000);
        
        // 210
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `${career || ''}`;
          if (!localStorage.getItem("fourthQuestion")) {
            localStorage.setItem("fourthQuestion", text);
          }
          this.fourthQuestion = text;
          this.addMessage('ai', text, 'text');
        }, 210000);
        
        // 270
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `${career || ''}`;
          if (!localStorage.getItem("fifthQuestion")) {
            localStorage.setItem("fifthQuestion", text);
          }
          this.fifthQuestion = text;
          this.addMessage('ai', text, 'text');
        }, 270000);
      } else {
        this.showStatus("SDK", 'error');
      }
    },
    
    // 
    backToHomeMessage() {
      this.$router.push('/home/message');
    },
    
    // 
    async sendMessage() {
      const prompt = this.message.trim();
      if (!prompt) return;
      
      this.addMessage('user', prompt, 'text');
      this.message = '';
      this.scrollToBottom();
      
      const typingId = this.addTypingIndicator();
      
      try {
        fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        }).then(response => {
          this.removeTypingIndicator(typingId);
          
          if (!response.ok) {
            throw new Error(`: ${response.status}`);
          }
          
          return response.json();
        }).then(data => {
          if (data.status === 'success') {
            this.addMessage('ai', data.result, 'text');
          } else {
            this.addMessage('ai', `: ${data.result}`, 'text', true);
          }
          this.scrollToBottom();
        }).catch(error => {
          this.removeTypingIndicator(typingId);
          this.addMessage('ai', `: ${error.message}`, 'text', true);
          this.scrollToBottom();
          console.error(':', error);
        });
      } catch (error) {
        this.removeTypingIndicator(typingId);
        this.addMessage('ai', `: ${error.message}`, 'text', true);
        this.scrollToBottom();
        console.error(':', error);
      }
    },
    
    // 
    checkRecordingSupport() {
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        console.log('');
        navigator.mediaDevices.getUserMedia({ audio: true })
          .then(() => {
            console.log('');
          })
          .catch(error => {
            console.warn(':', error);
            this.showStatus('', 'info');
          });
      } else {
        this.showStatus('', 'error');
      }
    },
    
    // 
    async toggleRecording() {
      if (this.isRecording) {
        this.stopRecording();
      } else {
        try {
          if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
            throw new Error('');
          }
          
          if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
            this.showStatus('HTTPS', 'error');
            return;
          }
          
          this.mediaStream = await navigator.mediaDevices.getUserMedia({ 
            audio: {
              sampleRate: 16000,
              channelCount: 1,
              echoCancellation: true,
              noiseSuppression: true,
              autoGainControl: true
            } 
          });
          
          // 
          const possibleMimeTypes = [
            'audio/wav;codecs=PCM',
            'audio/webm',
            'audio/mp4',
            'audio/mpeg'
          ];
          
          // 
          let mimeType = possibleMimeTypes.find(type => MediaRecorder.isTypeSupported(type));
          
          // 
          const options = mimeType ? { mimeType } : {};
          
          if (!mimeType) {
            console.warn('');
            this.showStatus('', 'warning');
          } else if (mimeType !== 'audio/wav;codecs=PCM') {
            console.warn(`audio/wav;codecs=PCM${mimeType}`);
            this.showStatus(`${mimeType}`, 'info');
          }
          
          this.mediaRecorder = new MediaRecorder(this.mediaStream, options);
          this.audioChunks = [];
          
          this.mediaRecorder.addEventListener('dataavailable', (event) => {
            this.audioChunks.push(event.data);
          });
          
          this.mediaRecorder.addEventListener('stop', async () => {
            // MIMEBlob
            const audioBlob = new Blob(this.audioChunks, { 
              type: mimeType || 'audio/wav' 
            });
            this.audioBlob = await this.convertToValidWav(audioBlob);
            
            if (this.mediaStream) {
              this.mediaStream.getTracks().forEach(track => track.stop());
              this.mediaStream = null;
            }
            
            if (this.audioBlob) {
              this.addVoiceMessage();
            }
          });
          
          this.mediaRecorder.start();
          this.isRecording = true;
          this.recordingSeconds = 0;
          
          this.recordingTimer = setInterval(() => {
            this.recordingSeconds++;
            if (this.recordingSeconds >= 60) {
              this.stopRecording();
            }
          }, 1000);
          
          this.showStatus('...', 'info');
          
        } catch (error) {
          console.error(':', error);
          this.isRecording = false;
          
          // 
          if (error.name === 'NotAllowedError') {
            this.showStatus('', 'error');
          } else if (error.name === 'NotFoundError') {
            this.showStatus('', 'error');
          } else if (error.name === 'NotReadableError') {
            this.showStatus('', 'error');
          } else if (error.name === 'NotSupportedError') {
            this.showStatus('', 'error');
          } else {
            this.showStatus(`: ${error.message}`, 'error');
          }
        }
      }
    },
    
    // 
    stopRecording() {
      if (this.isRecording && this.mediaRecorder) {
        this.mediaRecorder.stop();
        this.isRecording = false;
        
        if (this.recordingTimer) {
          clearInterval(this.recordingTimer);
          this.recordingTimer = null;
        }
        
        if (this.mediaStream) {
          this.mediaStream.getTracks().forEach(track => track.stop());
          this.mediaStream = null;
        }
        
        this.showStatus('', 'success');
      }
