    // 
    addVoiceMessage() {
      if (!this.audioBlob) return;
      
      const audioUrl = URL.createObjectURL(this.audioBlob);
      const duration = this.recordingSeconds;
      
      const audio = new Audio(audioUrl);
      
      this.messages.push({
        sender: 'user',
        type: 'voice',
        blob: this.audioBlob,
        url: audioUrl,
        duration: duration,
        isPlaying: false,
        textResult: null,
        toneResult: null,
        speechSpeed: null,
        logicResult: null
      });
      
      this.audioElements[this.messages.length - 1] = audio;
      
      this.scrollToBottom();
    },
    
    // 
    toggleVoicePlayback(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice') return;
      
      const audio = this.audioElements[index];
      if (!audio) return;
      
      if (msg.isPlaying) {
        audio.pause();
        msg.isPlaying = false;
      } else {
        this.messages.forEach((m, i) => {
          if (m.isPlaying && i !== index) {
            m.isPlaying = false;
            const otherAudio = this.audioElements[i];
            if (otherAudio) otherAudio.pause();
          }
        });
        
        audio.play()
          .then(() => {
            msg.isPlaying = true;
            audio.onended = () => {
              msg.isPlaying = false;
            };
          })
          .catch(error => {
            console.error(':', error);
            this.showStatus('', 'error');
          });
      }
    },
    
    // 
    async convertVoiceToText(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'voice' || !msg.blob) return;
      
      this.showStatus('...', 'info');
      
      try {
        const formData = new FormData();
        formData.append('audio', msg.blob, 'recording.wav');
        
        const response = await fetch(`${this.API_BASE_URL}/recognize_speech`, {
          method: 'POST',
          headers: this.commonHeaders,
          body: formData
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error('API:', errorText);
          throw new Error(`: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 
          this.messages[index] = {
            ...this.messages[index],
            textResult: data.text || ''
          };
          
          // 
          this.transcribeCount++;
          
          // 
          if (this.transcribeCount === 1 && !localStorage.getItem("firstResult")) {
            localStorage.setItem("firstResult", data.text || '');
            this.firstResult = data.text || '';
