          } else if (this.transcribeCount === 2 && !localStorage.getItem("secondResult")) {
            localStorage.setItem("secondResult", data.text || '');
            this.secondResult = data.text || '';
            console.log(''+localStorage.getItem("secondResult"));
          } else if (this.transcribeCount === 3 && !localStorage.getItem("thirdResult")) {
            localStorage.setItem("thirdResult", data.text || '');
            this.thirdResult = data.text || '';
            console.log(''+localStorage.getItem("thirdResult"));
          } else if (this.transcribeCount === 4 && !localStorage.getItem("fourthResult")) {
            localStorage.setItem("fourthResult", data.text || '');
            this.fourthResult = data.text || '';
            console.log(''+localStorage.getItem("fourthResult"));
          } else if (this.transcribeCount === 5 && !localStorage.getItem("fifthResult")) {
            localStorage.setItem("fifthResult", data.text || '');
            this.fifthResult = data.text || '';
            console.log(''+localStorage.getItem("fifthResult"));
