<template>
    <!--  -->
    <div id="bottom_bar">
        <van-tabbar v-model="active" :placeholder="true" @change="onChange">
            <van-tabbar-item name="message" icon="chat-o" to="/home/message" replace></van-tabbar-item>
            <van-tabbar-item name="friends" icon="friends-o" to="/home/friends" replace></van-tabbar-item>
            <van-tabbar-item name="discover" icon="underway-o" to="/home/discover" replace></van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            active: 'message' //messge
        };
    },
    methods: {
        // setTitleactiveHomeView
        onChange() {
            this.$emit('setTitle', this.active);
        }
    },
    // 
    mounted() {
        if(this.$route.path == '/home/message') {
            this.active = 'message';
        }
        else if(this.$route.path == '/home/friends') {
            this.active = 'friends';
        }
        else if(this.$route.path == '/home/discover') {
            this.active = 'discover';
        }
    }
}
</script>

