<template>
    <!--  -->
    <div id="login">
        <!--  -->
        <div class="bg-decoration top-left"></div>
        <div class="bg-decoration bottom-right"></div>
        
        <div id="login_error" @click="closeError()" class="error-toast"></div>
        
        <!-- Logo -->
        <div class="logo-container">
            <div class="logo-icon">
                <span class="icon-inner"></span>
            </div>
            <h1 class="logo-text"></h1>
            <p class="logo-subtitle"></p>
        </div>
        
        <!-- / -->
        <van-tabs 
            v-model="activeTab" 
            title-active-color="#4e5ba6" 
            class="login-form"
            @change="handleTabChange"
        >
            <van-tab title="">
                <van-form @submit="handleLogin" class="form-content">
                    <van-cell-group inset class="form-group">
                        <van-field 
                            v-model="loginForm.username"
                            name="username"
                            label=""
                            placeholder=""
                            :rules="[{ required: true, message: '' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="loginForm.password"
                            type="password"
                            name="password"
                            label=""
                            placeholder=""
                            :rules="[{ required: true, message: '' }]"
                            class="form-field"
                        />
                    </van-cell-group>
                    <div class="form-actions">
                        <a href="#" class="forgot-link">?</a>
                    </div>
                    <div class="login-button-container">
                        <van-button round type="primary" native-type="submit" class="login-button">
                             <van-icon name="arrow-right" size="16" />
                        </van-button>
                    </div>
                </van-form>
            </van-tab>
            
            <van-tab title="">
                <van-form @submit="handleRegister" class="form-content">
                    <van-cell-group inset class="form-group">
                        <van-field
                            v-model="registerForm.username"
                            name="username"
                            label=""
                            placeholder=""
                            :rules="[{ required: true, message: '' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="registerForm.password"
                            type="password"
                            name="password"
                            label=""
                            placeholder=""
                            :rules="[{ required: true, message: '' }]"
                            class="form-field"
                        />
                        <van-field
                            v-model="registerForm.email"
                            name="email"
                            label=""
                            placeholder=""
                            :rules="[
                                { required: true, message: '' },
                                { type: 'email', message: '' }
                            ]"
                            class="form-field"
                        />
                    </van-cell-group>
                    <div class="login-button-container">
                        <van-button round type="primary" native-type="submit" class="login-button">
                             <van-icon name="user-plus" size="16" />
                        </van-button>
                    </div>
                </van-form>
            </van-tab>
        </van-tabs>
        
        <!--  -->
        <div class="footer-info">
            <p>  2025 </p>
            
        </div>
    </div>
</template>

<script>
import { Button, CellGroup, Field, Form, Icon, Tabs, Tab, Toast } from 'vant';
import axios from 'axios';

// API
const API_BASE_URL = 'https://123.56.203.202';

// axios
const api = axios.create({
    baseURL: API_BASE_URL,
    timeout: 30000,
    withCredentials: true
});

export default {
    components: {
        VanButton: Button,
        VanCellGroup: CellGroup,
        VanField: Field,
        VanForm: Form,
        VanIcon: Icon,
        VanTabs: Tabs,
        VanTab: Tab
    },
    data() {
        return {
            activeTab: 0, // 0: , 1: 
            loginForm: {
                username: "",
                password: ""
            },
            registerForm: {
                username: "",
                password: "",
                email: ""
            }
        };
    },
    methods: {
        // 
        async handleLogin(values) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/sqlLogin', {
                    username: values.username,
                    password: values.password
                });
                
                Toast.clear();
                
                if (data.success) {
                    localStorage.setItem('currentUser', JSON.stringify({
                        username: data.username,
                        id: data.id
                    }));
                    localStorage.setItem('username', data.username);
                    
                    this.$router.push('/home/message');
                    Toast.success('');
                } else {
                    Toast.fail(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast.fail('');
            }
        },
        
        // 
        async handleRegister(values) {
            try {
                Toast.loading({
                    message: '...',
                    forbidClick: true,
                });
                
                const { data } = await api.post('/register', {
                    username: values.username,
                    password: values.password,
                    email: values.email
                });
                
                Toast.clear();
                
                if (data.success) {
                    Toast.success('');
                    this.activeTab = 0;
                } else {
                    Toast.fail(data.message || '');
                }
            } catch (error) {
                Toast.clear();
                console.error('', error);
                Toast.fail('');
            }
        },
        
        closeError() {
            document.getElementById('login_error').innerHTML = "";
        },
        
        // 
        handleTabChange() {
            const formEl = document.querySelector('.form-content');
            formEl.classList.add('tab-transition');
            setTimeout(() => {
                formEl.classList.remove('tab-transition');
            }, 300);
        }
    }
}
</script>

