<template>
    <!--  -->
    <div id="home">
        <top-bar :title="topbar_title"/>
        <van-pull-refresh v-model="loading" loading-text="..." success-text="" @refresh="onRefresh">
            <!-- router-view -->
            <!-- keep-alive -->
            <router-view v-slot="{ Component }">
                <keep-alive>
                    <component :is="Component" />
                </keep-alive>
            </router-view>
        </van-pull-refresh>
        <bottom-bar @setTitle="setTitle"/>
    </div>
</template>

<script>
import TopBar from '../components/TopBar.vue'
import BottomBar from '../components/BottomBar.vue'
export default {
    data() {
        return {
            loading: false, //
            topbar_title: "" //
        };
    },
    methods: {
        // 
        onRefresh() {
            setTimeout(() => {
                this.loading = false;
            }, 1000);
        },
        // BottomBar
        setTitle(data) {
            this.topbar_title = data;
        }
    },
    components: {
        TopBar,
        BottomBar
    }
}
</script>

