<template>
    <!--  -->
    <div id="side">
        <div id="side_top">
            <div id="side_top_inside">
                <div id="side_top_in1">
                    <van-icon :name="require('@/assets/icon/icon_side_.png')" size="22" style="margin:3px 20px 3px 0;" />
                    <van-icon name="cross" size="25" color="white" style="float:right;" @click="back()" />
                </div>
                <div id="side_top_in2">
                    <van-image
                        round
                        width="60px"
                        height="60px"
                        :src="require('@/assets/images/my_profile.jpg')"
                        @click="openMyCard()"
                        style="border:2px solid white; float:left; margin-right:15px;" />
                    <span style="font-size:18px;">{{ username || '' }}</span>
                    <van-icon :name="require('@/assets/icon/icon_side_.png')" size="20" style="float:right; padding:0 5px;" /><br>
                    <van-image :src="require('@/assets/images/qq.png')" style="width:170px;" /><br>
