                    <span style="margin:0 10px;"></span>
                </div>
            </div>
        </div>
        <div id="side_content">
            <ul>
                <!--  -->
                <li v-for="(poetry, index) in poetryList" :key="index">
                    <van-cell center is-link :title="poetry.content" style="padding:10px 20px;">
                        <template #icon>
                            <van-icon :name="poetry.icon" size="20" style="margin-right:15px" />
                        </template>
                    </van-cell>
                </li>
            </ul>
        </div>
        <div id="side_bottom">
            <div id="side_bottom_inside">
                <van-row>
                    <van-col span="6" style="text-align:center;">
                        <van-icon name="book" size="20" /><br>
                    </van-col>
                    <van-col span="6" style="text-align:center;">
                        <van-icon name="pen" size="20" /><br>
                    </van-col>
                    <van-col span="6" style="text-align:center;">
                        <van-icon name="history" size="20" /><br>
                    </van-col>
                    <van-col span="6" style="text-align:center;">
                        <van-icon name="share" size="20" /><br>
                    </van-col>
                </van-row>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            username: '',
            //  - 
            poetryList: [
                {  content: "" }, // 
                { icon: 'road', content: "" }, // 
                { icon: 'moon', content: "" }, // 
                { icon: 'boat', content: "" }, // 
                { icon: 'teardrop', content: "" }, // 
                { icon: 'laugh', content: "" }, // 
                { icon: 'sword', content: "" }, // 
                { icon: 'wine', content: "" }, // 
                { icon: 'cloud', content: "" }, // 
                { icon: 'leaf', content: "" }, // 
                { icon: 'mountain', content: "" } // 
            ]
        };
    },
    methods: {
        // 
        back() {
            history.back();
        },
        // 
        openMyCard() {
            this.$router.push('/card?profile=my_profile.jpg&name='+ (this.username || '') +'&qq=1164585349&gender=&age=21&birth_month=10&birth_day=22&constellation=&place=&sign=');
        }
    },
    mounted() {
        // localStorage
        const userInfo = localStorage.getItem('currentUser');
        if (userInfo) {
            try {
                const parsedUser = JSON.parse(userInfo);
                this.username = parsedUser.username || '';
            } catch (error) {
                console.error('', error);
                this.username = '';
            }
        }
    }
}
</script>

